body{
  background-color: crimson;
}

.App{
  background-color: darkmagenta;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

#to-do-form input{
  background-color: darkslategrey;
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: antiquewhite;
}

#to-do-form input::placeholder{
  color:rgba(14, 12, 12, 0.5);
}

#to-do-form input::focus{
  outline: none;
}

#to-do-form button{
  height:50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: darkgoldenrod;
  cursor: pointer;
  font-weight: bolder;
  color: #ffffff;
}

